export function playSelectedCard(e) {
  const is_selector = domfunctions.checkIfIncludesClass(e.target , "selector")
if(!is_selector){return}
  const selector = e.target
  const card = selectedCard.cardObject
  const entity = selector.parentNode.entity
  card.play(entity)

}


export function damage(recievingEntity , DealingEntity , initialDamage) {


  let damageObj ={ val : basicDamageFormula(recievingEntity , DealingEntity , initialDamage),
                status: true}


// loop through dealDamage Effects
  for(const effect in DealingEntity.dealDamageEffects ){
    DealingEntity.dealDamageEffects[effect].listEffect(damageObj)
  }

// loop through recieveDamageEffects Effects
  for(const effect in recievingEntity.recieveDamageEffects){
    recievingEntity.recieveDamageEffects[effect].listEffect(damageObj)
  }

if (damageObj.status === false) { return }

// console.log(`attack function : ${recievingEntity.name} recieved ${damageObj.val}`
// , recievingEntity);
  let unblockedDamage = damageBlock(damageObj.val , recievingEntity)
  recievingEntity.health -= unblockedDamage
  animationHandler(recievingEntity)
  renderHealth(recievingEntity)
}


export function directlyDamage( damage , entity) {
  entity.health -= damage
  renderHealth(entity)
}

export function damageBlock(damage , entity){
  const block = entity.block
    if(block >= damage){
      entity.block -= damage
      return 0
    }

    else{
      entity.block = 0
      return (damage - block)
    }
}

export function basicDamageFormula(recievingEntity , DealingEntity , initialDamage) {
  let finalDamage = (initialDamage + DealingEntity.str ) *
           (DealingEntity.atkMod * recievingEntity.defMod )
  return Math.round(finalDamage)
}

// /play cardon enemy




// enemy functions
export function attackFriendly(dealingDamage , DealingEntity) {
  // first ally from entity spot()
  damage(player , DealingEntity , dealingDamage)
}

export function gainBlock( initialBlock , recievingEntity) {
  let block = {val : initialBlock ,
               status : true}

  for(const effect in recievingEntity.blockEffects ){
    recievingEntity.blockEffects[effect].listEffect(block)
  }

  if(block.status === false){return}
  
  recievingEntity.block += block.val
  renderHealth(recievingEntity)
}

